.filterWrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;

  padding: 20px;
  background-color: azure;
  border: 2px solid skyblue;
}
.filterLabel {
  font-style: italic;
  font-weight: 600;
  padding: 5px;
}
.filterInput {
  margin: 5px;
  box-shadow: 8px 2px 12px 5px #818180;
  border: 1px solid seagreen;
  border-radius: 5%;

  padding: 4px;
  font-size: 15px;
}
